.cart-drawer {
	.modal-dialog {
		max-width: calc-em(375);
	}

	.modal-dialog,
	.modal-content {
		height: 100%;
	}

	&:not(.show) .modal-dialog {
		transform: translate(100%, 0);
	}

	.close {
		top: 0;
		right: 0;
		outline: none;
		opacity: 1;

		svg path {
			fill: $body-color;
		}
	}

	.progress {
		height: 5px;
	}

	hr {
		border-color: $border-color;
	}

	&__shopify-icon {

		&:before,
		&:after {
			content: '';
			width: 25%;
			border-bottom: $border-width solid $border-color;
		}
	}

	.modal-footer>* {
		margin: 0;
	}
}

#dynamic-checkout-cart {
	ul {
		margin: 0px !important;
	}

	li {
		display: none !important;
		margin: 0px !important;
		flex-basis: 100% !important;
		height: 50px !important;
		&.show {
			display: block !important;
		}
	}

	.paypalLight {
		min-height: 50px !important;
		max-height: 50px !important;
		height: 50px !important;
	}
}

.manual-gwp {
	&__container {
		overflow: hidden;
		scroll-behavior: smooth;
	}

	&__left,
	&__right {
		top: 0;
	}

	&__left {
		right: 2em;
	}

	&__right {
		right: 0.5em;
	}

	&__item {
		flex: 0 0 calc-em(96);
		width: calc-em(96);

		picture {
			padding: 0 $grid-gutter-width / 2;
		}

		figcaption {
			font-size: $font-size-xs;
			background: $gray-200;
			padding: map-get($spacers, 1) / 2;
			border-radius: 2px;
		}

		button {
			&:focus,
			&:active {
				box-shadow: none !important;
			}
		}
	}
}
