.review-carousel {

	&__decoration {
		font-size: calc-em(60, 16);
		right: -(calc-em(5, 16));
		left: auto;

		@include media-breakpoint-up(lg) {
			font-size: calc-em(94, 16);

			&-top {
				right: 0;
				transform: scaleY(-1);
				z-index: 1; //to be above carousel
			}

			&-bottom {
				transform: scaleY(-1) rotate(180deg);
			}
		}
	}

	.carousel-control-next,
	.carousel-control-prev {
		top: calc-em(260, 16);

		@include media-breakpoint-up(lg) {
			top: calc-em(245, 16);
		}
	}

}
